import { Component, OnInit } from '@angular/core';
import { SharedService } from 'src/service/SharedService';
import { HttpService } from 'src/service/http.service';
import { FormControl, Validators } from '@angular/forms';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-other-servies',
  templateUrl: './other-servies.component.html',
  styleUrls: ['./other-servies.component.css']
})
export class OtherServiesComponent implements OnInit {

  loading = false;
  buttionText = "Submit";
  name: "";
  company: "";
  email: "";
  phone: "";
  message: "";

  constructor(public objSharedService: SharedService, public http: HttpService) { }

  ngOnInit(): void {
  }

  submitDetails() {
    this.loading = true;
    this.buttionText = "Submiting...";
    let user = {
      name: this.name,
      company: this.company,
      email: this.email,
      phone: this.phone,
      message: this.message
    }
    // console.log("User Detail::",user);
    this.http.sendEmail("http://localhost:3000/sendmail", user).subscribe(
      data => {
        let res: any = data;
        // console.log(
        //   `ðŸ‘ > ðŸ‘ > ðŸ‘ > ðŸ‘ ${user.name} is successfully register and mail has been sent and the message id is ${res.messageId}`
        // );
      },
      err => {
        console.log(err);
        this.loading = false;
        this.buttionText = "Submit";
      }, () => {
        this.loading = false;
        this.buttionText = "Submit";
        Swal.fire({
          title: `Email Sent to ${this.name} :)`,
          icon: 'success'
        })
        this.name = "";
        this.company = "";
        this.email = "";
        this.phone = "";
        this.message = "";
      }
    );
  }

  returnHome() {
    this.objSharedService._IsMainPage = true;
    this.objSharedService._IsContactDev = false;
    this.objSharedService._showAdminSettigs = false;
    this.objSharedService._showLoginSettigs = false;
    this.objSharedService._showTimeline = false;
    this.objSharedService._showLoginForm = false;
    this.objSharedService._IsUnderConstuction = false;
    this.objSharedService._IsOtherServices = false;
    this.objSharedService._showServices=false;
    this.objSharedService._sendMail=false;
  }

  sendMail(){
    this.objSharedService._sendMail = true;
    this.objSharedService._showServices=false;
  }

  returnServices(){
    this.objSharedService._sendMail=false;
    this.objSharedService._showServices=true;
  }
}
